/**
 * GÃ¨re le focus clavier sur les Ã©lÃ©ments d'un bloc HTML spÃ©cifiÃ©.
 * @param key - L'Ã©vÃ©nement clavier qui a dÃ©clenchÃ© la fonction.
 * @param block - L'Ã©lÃ©ment HTML qui reprÃ©sente le bloc sur lequel on souhaite gÃ©rer le focus.
 */
export function focusInBlock(key:KeyboardEvent, block:HTMLElement):void
{
    key.preventDefault();
    const focusables = getFocusableElements(block);
    let index = focusables.findIndex(f => f === block.querySelector(':focus'));
    (key.shiftKey === true) ? index-- : index++;
    if (index >= focusables.length) { index = 0; }
    if (index < 0) { index = focusables.length - 1; }
    const FIELD = focusables[index] as HTMLElement;
    FIELD.focus();
}
/**
 * RÃ©cupÃ¨re tous les Ã©lÃ©ments focusables dans un bloc HTML spÃ©cifiÃ©.
 * @param block - L'Ã©lÃ©ment HTML qui reprÃ©sente le bloc contenant les Ã©lÃ©ments focusables.
 * @returns Un tableau d'Ã©lÃ©ments focusables prÃ©sents dans le bloc.
 */
export function getFocusableElements(block:HTMLElement):Element[]
{
    let focusableSelector = "button, select, input, a, textarea";
    return Array.from(block.querySelectorAll(focusableSelector));
}